@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Lato', sans-serif;
}

.header {
  background: #353546;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .left {
  color: white;
  font-weight: bold;
  display: block;
  margin-left: 10vw;
}

.header .right {
  margin-right: 10vw;
}

.subtitle {
  opacity: .5;
}

.app {
  text-align: center;
}

.fa-github {
  color: white;
  opacity: .4;
}

.fa-search {
  color: #353546;
  margin-right: 10px;
  cursor: pointer;
}

.search-box {
  background: white;
  opacity: .4;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  width: 340px;
}

.search-input {
  border: none;
  outline: none;
  margin-left: 10px;
  font-size: 1rem;
}

.user-list {
  list-style-type: none;
  padding: 0;
}

.user-row:first-of-type {
  border-top: solid 1px #eeeef3;
}

.user-row {
  height: 60px;
  display: flex;
  align-items: center;
  border-bottom: solid 1px #eeeef3;
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.count {
  color: #a4a4a9;
  margin: 4vh 0;
}

.user-row:hover {
  background: #eeeef3;
}

.avatar {
  border-radius: 50%;
  margin: 0 1vw 0 10vw;
}

.modal {
  position: fixed;
  /* background: white; */
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.modal .content {
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 2em;
  background: #ffffff;
  border: solid 1px #eeeef3;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 80%;
  min-height: 100px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.modal .fa-times {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
.user-details {
  display: flex;
}

.avatar-large {
  margin-top: 30px;
  width: 50%;
  height: auto;
}

.basic-info {
  margin: 0 40px;
}

.basic-info div {
  margin: 10px 0;
}

.basic-info .login {
  opacity: .4;
}

.user-details .left {
  border-right: solid 1px #eeeef3;
  max-width: 40%;
}

.user-details .right {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: row;
  width: 100%;
}

.column .number {
  font-size: 4rem;
}
